/* src/App.css */

/* Definizione delle variabili per colori e font */
:root {
  --primary-color: #0ff; /* Colore ciano neon */
  --secondary-color: #f0f; /* Colore magenta neon */
  --background-color: #0a0a0a; /* Sfondo scuro */
  --text-color: #ffffff; /* Colore testo bianco */
  --font-family: 'Orbitron', sans-serif; /* Font futuristico */
}

/* Stili globali */
body, html {
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: var(--font-family);
  text-align: center;
  overflow: hidden; /* Previene lo scrolling se la scena Ã¨ troppo grande */
}

/* Titolo */
h2 {
  font-size: 2rem;
  color: var(--primary-color);
  text-transform: uppercase;
  letter-spacing: 2px;
  margin: 20px 0;
  text-shadow: 0 0 10px var(--primary-color);
}

/* Contenitore principale */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
}

/* Contenitore AR */
.ar-container {
  margin-top: 15%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 80%;
  max-width: 800px;
  height: 60vh; /* Imposta un'altezza adeguata */
}

/* Marker Display */
.marker-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
}

.marker-display img {
  width: 250px;
  height: 250px;
  border: 2px solid var(--primary-color);
  border-radius: 10px;
  box-shadow: 0 0 20px var(--primary-color);
}

.marker-display p {
  margin-top: 10px;
  font-size: 1rem;
  color: var(--secondary-color);
}

/* Scena AR */
a-scene {
  width: 400px;
  height: 400px;
  border: 2px solid var(--secondary-color);
  border-radius: 10px;
  box-shadow: 0 0 20px var(--secondary-color);
}

/* Responsive design */
@media only screen and (max-width: 768px) {
  .ar-container {
    flex-direction: column;
    height: auto;
  }

  .marker-display {
    margin-right: 0;
    margin-bottom: 20px;
  }

  a-scene {
    width: 100%;
    height: 300px;
  }

  h2 {
    font-size: 1.5rem;
  }
}
